import Vue from 'vue';
import storage from './tool/storage.js';
import { BASE_URL, PROTOCOL, CONTAIHER_TOP } from '../config/index.js';
document.getElementById("login").style.top = CONTAIHER_TOP;
new Vue({
    el: '#login',
    data: function() {
        let email = "";
        let password = "";
        let rememberChecked = false;
        if(storage.getItem({name: "tz_login_email", storage: localStorage})) {
            email = storage.getItem({name: "tz_login_email", storage: localStorage});
        }
        if(storage.getItem({name: "tz_login_password", storage: localStorage})) {
            password = storage.getItem({name: "tz_login_password", storage: localStorage});
            rememberChecked = true;
        }
        return {
            email: email,
            password: password,
            captcha: "",
            captcha_src: "",
            rememberChecked: rememberChecked,
            error: false,
            error_msg: ""
        };
    },
    methods: {
        switchCaptchaSrc() {
            $.get(PROTOCOL+"//"+BASE_URL+"/verification_code",(data) => {
                if(data.code==1) {
                    this.captcha_src = data.data.src;
                }
            });
        },
        login() {
            $.post(PROTOCOL+"//"+BASE_URL+"/auth/login",{
                login_name: this.email,
                password: this.password,
                captcha: this.captcha
            },(data) => {
                if(data.code==1) {
                    // alert("登陆成功");
                    if(this.rememberChecked) {
                        storage.setItem({
                            name: "tz_login_email",
                            data: this.email,
                            storage: localStorage
                        });
                        storage.setItem({
                            name: "tz_login_password",
                            data: this.password,
                            storage: localStorage
                        });
                    } else {
                        storage.removeItem({
                            name: "tz_login_password",
                            storage: localStorage
                        });
                    }
                    location.href = PROTOCOL+"//"+BASE_URL+"/tz/member92019.html";
                } else {
                    this.switchCaptchaSrc();
                    this.error = true;
                    this.error_msg = data.msg;
                }
            });
        },
        remember() {
            this.rememberChecked = !this.rememberChecked;
        }
    },
    mounted() {
        $.get(PROTOCOL+"//"+BASE_URL+"/verification_code",(data) => {
            if(data.code==1) {
                this.captcha_src = data.data.src;
            }
        });
    }
});