export default {
    getItem({name,storage}) {
        if(!storage.getItem(name)) {
            return false;
        }
       let data = JSON.parse(storage.getItem(name));
       let nowTime = Math.round(new Date().getTime()/1000);
       if(nowTime < data.expired) {
            return data.data;
       } else {
            storage.removeItem(name);
            return false;
       }
    },
    setItem({name, data, storage, expired = Math.round(new Date().getTime()/1000) + 86400 * 7}) {
        let setData = {
            data,
            expired
        };
        storage.setItem(name, JSON.stringify(setData));
    },
    removeItem({name,storage}) {
        storage.removeItem(name);
    }
}